@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  width: 1920px;
  max-width: 1920px;
}

@media screen and (max-width: 1920px) {
  .container {
    width: 100%;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Oswald', sans-serif;
}

.content-text {
  color: #6e774a;
  font-size: 18px;
  line-height: 2.222;
}

.content-text--faq {
  letter-spacing: 4px;
  padding-bottom: 15px;
  padding-top: 15px;
}

.page-title {
  letter-spacing: 4px;
  text-transform: uppercase;
  color: #1c1c1b;
  font-size: 30px;
  font-family: 'Oswald', sans-serif;
}

.page-title--small {
  font-size: 18px;
  letter-spacing: 2px;
}

.button-text {
  font-size: 20px;
  text-transform: uppercase;
  color: white;
}

.button-line {
  width: 45px;
  height: 2px;
  background-color: #fff;
  margin-left: 10px;
  margin-right: 10px;
}
/*# sourceMappingURL=App.css.map */
.header-line {
  transform: rotate(-90deg);
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 150px;
  left: -100px;
}

.header-line__text {
  font-size: 26px;
  color: white;
  text-transform: uppercase;
}

.header-line__line {
  width: 100px;
  height: 2px;
  background-color: #fff;
  margin-right: 30px;
}

.header {
  width: 100%;
  position: relative;
  font-family: 'Oswald', sans-serif;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(/static/media/header.7f8fb2f2.png);
}

.header__text-block {
  text-align: right;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  width: 670px;
  margin-left: auto;
}

.header__container {
  margin: auto;
  max-width: 1170px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
}

.header__filter {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.164);
}

.header__title {
  margin-bottom: 35px;
  text-transform: uppercase;
  font-size: 55px;
  letter-spacing: 15px;
  color: white;
}

.header__subtitle {
  font-weight: 300;
  font-size: 24px;
  letter-spacing: 5px;
  color: rgba(255, 255, 255, 0.712);
}

.header__subtitle--small {
  font-size: 18px;
  letter-spacing: 3px;
}

.header__button {
  margin-top: 50px;
  margin-bottom: 20px;
  width: 270px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: 1px white solid;
}

.nav {
  position: fixed;
  top: 0px;
  display: flex;
  width: 100%;
  height: 80px;
  border-bottom: 1px rgba(255, 255, 255, 0.842) solid;
}

.nav__logo {
  width: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  color: white;
  justify-self: center;
  text-transform: uppercase;
}

.nav__page {
  display: flex;
  padding-left: 10px;
  width: 45%;
}

.nav__user {
  margin-left: auto;
  width: 45%;
  display: flex;
  justify-content: flex-end;
}

.nav__item {
  width: 100px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  color: white;
  text-transform: uppercase;
}

.nav__button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  color: white;
  text-transform: uppercase;
  padding-left: 25px;
  padding-right: 25px;
  height: 100%;
  border-left: 1px rgba(255, 255, 255, 0.842) solid;
}

.nav__icon {
  margin-right: 9px;
}
/*# sourceMappingURL=Header.css.map */
.about-us {
  padding-top: 100px;
  font-family: 'Oswald', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1170px;
}

.about-us__title {
  letter-spacing: 3px;
  padding-bottom: 25px;
  font-size: 24px;
  color: #1c1c1b;
  text-align: center;
  text-transform: uppercase;
}

.about-us__title--left {
  text-align: left;
}

@media screen and (max-width: 1030px) {
  .about-us {
    padding-top: 40px;
  }
}

.logos-block {
  position: relative;
}

.logos-block__content {
  position: absolute;
  top: 20%;
  left: 12%;
}

.about-tabs {
  padding-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 1030px;
}

@media screen and (max-width: 1030px) {
  .about-tabs {
    width: 100%;
    padding-top: 20px;
  }
}

.about-tabs__tabs {
  justify-content: center;
  flex-wrap: wrap;
  display: flex;
}

.about-tabs__item {
  transition: all 0.6s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 340px;
  height: 60px;
  border-bottom: 2px #ced6af solid;
  margin-bottom: 80px;
}

.about-tabs__item:hover {
  cursor: pointer;
  background-color: #ebf5e1;
}

.about-tabs__item--active {
  border-bottom: 2px black solid;
}

@media screen and (max-width: 992px) {
  .about-tabs__item {
    margin-bottom: 20px;
  }
}

.about-tabs__content {
  flex-wrap: wrap;
  display: flex;
  justify-content: space-around;
}

.about-tabs__text {
  padding-bottom: 50px;
}

.about-tabs__info {
  width: 35%;
  padding-left: 15px;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 992px) {
  .about-tabs__info {
    width: 80%;
    padding-top: 30px;
  }
}

.about-tabs__images {
  padding-left: 20px;
  width: 50%;
}

@media screen and (max-width: 992px) {
  .about-tabs__images {
    text-align: center;
    width: 80%;
    padding-left: 0px;
  }
}

.about-tabs__img {
  object-fit: cover;
  padding-bottom: 15px;
  width: 570px;
  height: 300px;
}

@media screen and (max-width: 1100px) {
  .about-tabs__img {
    max-width: 100%;
  }
}
/*# sourceMappingURL=AboutUs.css.map */
.products {
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.products__slider {
  margin-top: 75px;
  padding-bottom: 100px;
  overflow: hidden;
  width: 100%;
  text-align: center;
}

.products__item {
  height: 500px;
}

.products__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 85%;
  border: 1px #ced6af solid;
}

.products__cart {
  width: 100%;
  height: 15%;
  background-color: #f1f1f1;
  border-bottom: #e1e1e1;
  border-left: #e1e1e1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.products__stars {
  display: flex;
  text-align: center;
}

.products__star {
  margin: 0 2px;
}

.products__name {
  margin-top: 15px;
  margin-bottom: 35px;
}

.products__price {
  padding-left: 30px;
}

.products__buy {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 31%;
  height: 100%;
  background-color: #6e774a;
}

.products__img-b {
  width: 200px;
  height: 230px;
}

.products__img {
  margin: auto;
  max-width: 100%;
  object-fit: cover;
}

.products .slick-slide {
  padding: 0 20px;
}

.products__next-arrow {
  position: absolute;
  bottom: -60px;
  right: 46%;
}

.products__prev-arrow {
  left: 46%;
  position: absolute;
  bottom: -60px;
}

.products__prev-arrow:hover, .products__next-arrow:hover {
  cursor: pointer;
}

.products__prev-arrow:active, .products__next-arrow:active {
  transform: scale(0.7, 0.7);
}

@media screen and (max-width: 1300px) {
  .products__next-arrow {
    right: 42%;
  }
  .products__prev-arrow {
    left: 42%;
  }
}
/*# sourceMappingURL=Products.css.map */
.faq {
  padding-top: 80px;
  padding-bottom: 140px;
  width: 570px;
}

@media screen and (max-width: 569px) {
  .faq {
    width: 100%;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 50px;
  }
}

.faq__open {
  margin-right: 20px;
}

.faq__title {
  text-align: center;
  padding-bottom: 45px;
}

.faq__question {
  padding-left: 10px;
}

.faq__question:hover {
  cursor: pointer;
  background-color: #f1f8ecea;
}

.faq__question:active {
  transition: 0;
  background-color: #e6ffd1;
}

.faq__block {
  height: 450px;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 568px) {
  .faq__block {
    height: auto;
  }
}

.faq__answer {
  overflow: hidden;
  transition: .7s all ease;
  height: 0px;
  font-size: 18px;
}

.faq__answer--active {
  height: auto;
  padding-left: 50px;
  padding-top: 20px;
  padding-bottom: 45px;
}

@media screen and (max-width: 569px) {
  .faq__answer--active {
    padding: 10px;
    text-align: center;
  }
}

.faq__container {
  border-top: #eaedde 1px solid;
  border-bottom: #eaedde 1px solid;
}
/*# sourceMappingURL=Faq.css.map */
.feedback {
  background-image: url("/img/feedback1.png");
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.feedback__slider {
  margin: auto;
  max-width: 1170px;
  overflow: hidden;
}

.feedback__item {
  height: 800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.feedback__content {
  padding-bottom: 70px;
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 569px) {
  .feedback__content {
    width: 90%;
  }
}

.feedback__stars {
  display: flex;
  justify-content: center;
}

.feedback__text {
  padding: 35px 0px;
  text-align: center;
  font-weight: 300;
  font-size: 22px;
  color: white;
  letter-spacing: 2px;
}

.feedback__text--name {
  padding: 0;
  font-size: 24px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
}

.feedback__star {
  margin: 0 3px;
}

.feedback__prev-arrow {
  z-index: 1;
  position: absolute;
  bottom: 0px;
  left: 50%;
  margin-left: -130px;
}

.feedback__next-arrow {
  z-index: 1;
  position: absolute;
  bottom: 0px;
  right: 50%;
  margin-right: -130px;
}
/*# sourceMappingURL=Feedback.css.map */
.footer {
  flex-wrap: wrap;
  padding-top: 200px;
  width: 1170px;
  display: flex;
}

@media screen and (max-width: 1170px) {
  .footer {
    width: 100%;
  }
}

.footer__info {
  width: 31%;
}

.footer__contacts {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 38%;
  height: 460px;
}

.footer__info-inner {
  padding-top: 90px;
  margin: auto;
}

.footer__text {
  text-align: center;
  letter-spacing: 1px;
  padding: 12px 0px;
  font-weight: 300;
  font-size: 14px;
  color: #6e774a;
  text-transform: uppercase;
}

.footer__logo {
  position: relative;
  bottom: 30px;
  line-height: 100%;
  text-align: center;
  font-size: 60px;
  color: #6e774a;
  text-transform: uppercase;
  padding-bottom: 20px;
}

.footer__social {
  margin-left: 15px;
  width: 180px;
  display: flex;
  justify-content: space-between;
}

.footer__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__title {
  padding-bottom: 30px;
  font-size: 18px;
  text-transform: uppercase;
}

.footer__input-block {
  position: relative;
}

.footer__input {
  font-weight: 300;
  font-size: 16px;
  padding-left: 30px;
  background-color: transparent;
  border: 2px solid #ced6af;
  width: 370px;
  height: 60px;
}

.footer__input:focus {
  outline: none;
}

.footer__submit {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0;
  border-left: 2px solid #ced6af;
  background-color: transparent;
  bottom: 5px;
  position: absolute;
  right: 0px;
  width: 80px;
  height: 50px;
}

.footer__submit:focus {
  outline: none;
}

.footer__copy {
  padding-bottom: 50px;
  color: #6e774a;
  letter-spacing: 1px;
  font-size: 11px;
  text-transform: uppercase;
}

.footer__line-h {
  width: 100%;
  height: 2px;
  background-color: #6e774a;
}

.footer__line-v {
  height: 55px;
  width: 2px;
  background-color: #6e774a;
}
/*# sourceMappingURL=Footer.css.map */
